.header {
  background: #fff;
}
.header .header-top {
  height: 60px;
  line-height: 60px;
  padding: 0 20px;
  text-align: right;
}
.header .header-top a {
  margin-left: 20px;
}
.header .breadcrumb {
  height: 40px;
  line-height: 40px;
  padding: 0 20px;
  border-top: 1px solid #f9c700;
  font-size: 12px;
}
.header .breadcrumb .breadcrumb-title {
  text-align: center;
  font-size: 18px;
  position: relative;
}
.header .breadcrumb .breadcrumb-title:after {
  position: absolute;
  content: '';
  left: 125px;
  top: 39px;
  border: 10px solid #fff;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 10px solid transparent;
}
.header .breadcrumb .weather {
  text-align: right;
}
.header .breadcrumb .weather .date {
  margin-right: 10px;
}
.header .breadcrumb .weather .weather-img img {
  height: 25px;
}
.header .breadcrumb .weather .weather-detail {
  margin-left: 5px;
}
.simple-page .header-top {
  background: #1890ff;
  color: #fff;
}
.simple-page .header-top .img-logo img {
  height: 40px;
  vertical-align: middle;
  margin-right: 10px;
}
.simple-page .header-top .img-logo span {
  font-size: 18px;
}

/** 常用色值 **/
/** 常用字体大小 **/
/** load **/
.ajax-loading {
  display: none;
}
.ajax-loading .loading {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 0 40px;
  height: 80px;
  line-height: 80px;
  background: rgba(0, 0, 0, 0.75);
  border-radius: 6px;
  text-align: center;
  z-index: 9999;
  font-size: 16px;
  color: #fff;
}
.ajax-loading .loading img {
  width: 32px;
  vertical-align: middle;
}
.ajax-loading .loading span {
  margin-left: 12px;
}
.ajax-loading .overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 9998;
  background: #ffffff;
  opacity: 0.1;
}
