.login-page .footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 60px;
  padding-top: 20px;
  background: rgba(21, 23, 20, 0.5);
  font-size: 12px;
  letter-spacing: 0.15em;
}
.login-page .ant-input-lg,
.login-page .ant-btn-lg {
  height: 40px;
  font-size: 16px;
}
.login-page .login-form-button {
  width: 100%;
  margin-top: 15px;
}
.login-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  line-height: 80px;
  background: rgba(21, 20, 13, 0.9);
}
.login-header .logo {
  padding-left: 100px;
  font-size: 20px;
  color: white;
}
.login-header img {
  height: 40px;
  margin-right: 15px;
}
.login-content-wrap {
  min-height: 550px;
  height: calc(100vh);
  background: url(https://media.xiaohuali.com/ebike-fc/login/banner.jpg) no-repeat center;
  background-size: cover;
}
.login-content {
  position: relative;
  top: 50%;
  display: -webkit-flex;
  display: flex;
  max-width: 960px;
  margin: 0 auto;
  padding: 0 20px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.login-content .word {
  -webkit-flex: 7 1;
          flex: 7 1;
  line-height: 1.4;
  color: #fff;
  font-size: 64px;
  font-weight: bold;
}
.login-box {
  position: relative;
  -webkit-flex: 4 1;
          flex: 4 1;
  height: 370px;
  padding: 40px 30px 0 30px;
  background: #fff;
  overflow: hidden;
}
.login-box .title {
  line-height: 1;
  margin-bottom: 50px;
  font-size: 40px;
  color: #333;
  font-weight: bold;
  text-align: center;
}
.login-box .error-msg-wrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.login-box .error-msg-wrap div {
  visibility: hidden;
  height: 30px;
  line-height: 30px;
  background: #f60c1a;
  color: #fff;
  font-size: 16px;
  text-align: center;
  -webkit-transform: translateY(-30px);
          transform: translateY(-30px);
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
.login-box .error-msg-wrap div.show {
  visibility: visible;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
@media screen and (max-width: 1200px) {
  .login-header {
    padding-left: 30px;
    padding-right: 30px;
  }
  .login-content .word {
    font-size: 48px;
  }
}

